@import "./../../../Resources//Sass//utilities/variables";
#sign-layout {
  display: flex;
  background: #7fffd426;
  & > * {
    width: 50%;
  }
}

.sign-in {
  font-family: Dosis, sans-serif;
  color: #464646;
  & > * {
    font-family: inherit;
  }
}

#forget-password {
  color: $primary-color;
}

@media (max-width: 980px) {
  #sign-layout {
    flex-direction: column;
    & > * {
      width: 100%;
    }
  }
}
