@import "./../../../Resources/Sass/utilities/mixins";

//messages
.messages {
  display: none;
  background: #6ddba4;
  .message {
    color: #fff;
    position: relative;
    font-size: 1.8rem;
    margin-bottom: 10px;
    .close-btn {
      @include absolute-center;
      left: 96%;
      top: 55%;
      cursor: pointer;
      ion-icon {
        font-size: 45px;
        --ionicon-stroke-width: 18px;
      }
    }
  }
  #signInLink {
    display: none;
  }
}
