@import "./../../../Resources//Sass//utilities/variables";
@import "./../../../Resources//Sass//utilities/mixins";

.showcase-grid-item {
  position: relative;
  display: flex;
  cursor: pointer;
  overflow: hidden;
  &:hover &-content {
    background: #36e28c2a;
    transform: scale(2.1);
  }
  &-img {
    width: 100%;
  }
  &-content {
    position: absolute;
    @include flex-center;
    font-family: "Parisienne", cursive;
    font-size: 4rem;
    opacity: 1;
    transition: 0.4s;
    transform: scale(2);
    background: #36e28c18;
    width: 100%;
    height: 100%;

    #text {
      position: absolute;
      z-index: 2;
      color: #000e07ad;
    }
    ion-icon {
      font-size: 2rem;
    }
  }
}

@media (max-width: 568px) {
  .showcase-grid-item {
    &:hover &-content {
      transform: scale(1.5);
    }
    &-content {
      transform: scale(1.4);
    }
  }
}
