@import "./../../../Resources//Sass/utilities/variables";
@import "./../../../Resources/Sass/utilities/mixins";

.header {
  margin: 0 50px;
  &-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-logo img {
    height: 100px;
    width: auto;
  }
}

#menu-icon {
  cursor: pointer;
  border: 1.5px solid $menu-color;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  ion-icon {
    color: $menu-color;
  }
}

body {
  transition: 0.7s;
}

body.open {
  transform: translateX(-250px);
  overflow: hidden;
}

ul.side-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 250px;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1;
  background: #f5f5f5;
  transform: translateX(100%);
  transition: 0.7s;
  img {
    width: 100%;
  }
  li {
    list-style: none;
    text-align: center;
    @include flex-center;
    &::not(:last-child) {
      margin-bottom: none;
    }
    a {
      text-decoration: none;
      text-transform: uppercase;
      font-family: "Dosis", sans-serif;
    }
  }
}

@media (max-width: 768px) {
  .header {
    margin: 0 10px;
  }
  .header-logo img {
    height: 80px;
  }
  #menu-icon {
    height: 45px;
    width: 45px;
  }
}

@media (max-width: 468px) {
  .header-logo img {
    height: 70px;
  }
}
