.form-group {
  margin: 1.4rem 0;
  input {
    border: none;
    height: 40px;
    background: #c3c3c329;
    padding: 0px 19px;
    display: block;
    border-radius: 100px;
    width: 100%;
    color: #333;
    font-family: Quicksand;
    font-size: 1.05rem;
    transition: 0.1s;
    &:focus {
      outline: none;
    }
    &:hover {
      opacity: 0.9;
      transform: scale(1.01);
    }
  }
  label {
    padding-left: 7px;
  }
  & > * {
    display: block;
  }
}
